@use "../../sass/typography";

#footer {
	height: 110px;
	border-top: 1px solid #d1d1d1;
	background: #f4f4f4;

	#copyRight {
		color: #777;
	}
	#contact {
		padding-top: 8px;
		padding-top: 0.5em;
	}

	h2,
	span,
	a {
		@include typography.text-size-3;

		font-weight: normal;
	}
}

.footer-updated-on {
	color: #333;
}
