@use "../../sass/colors";
@use "../../sass/functions";
@use "../../sass/typography";
@use "../../sass/layout";

#introduction {
	color: #fff;
	position: relative;
	background-color: #333;
	padding-left: var(--spacing-6);
	padding-right: var(--spacing-6);

	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		font-weight: normal;
	}

	h1,
	p {
		margin: 0;

		-webkit-touch-callout: auto;
		-webkit-user-select: auto;
		-moz-user-select: auto;
		-ms-user-select: auto;
		user-select: auto;
	}

	.linkContainer {
		margin-bottom: 0;

		a {
			$color: colors.$blue-light;
			$color-dark: functions.fixed-darken($color);

			color: $color;

			&:after {
				border-color: $color $color transparent transparent;
			}

			&:hover {
				color: $color-dark;

				&:after {
					border-color: $color-dark $color-dark transparent transparent;
				}
			}
		}

		@media (min-width: 992px) {
			justify-content: center;
		}
	}
}

.portfolio {
	.imageHolder {
		width: 100%;
	}
	@media (min-width: 992px) {
		.imageHolder {
			width: auto;
		}
	}

	.sectionContainer {
		padding: 5em 0;

		.linkContainer {
			margin-bottom: 0;
		}
	}

	#newsSectionContainer .images {
		padding-top: 20px;
		background-color: white;
		border: 1px solid #eee;
	}

	@media (min-width: 992px) {
		.primaryImage {
			display: none;
		}
	}
	.secondaryImage {
		display: none;

		@media (min-width: 992px) {
			display: inline-block;
		}
	}

	.listContainer {
		display: inline-block;
		margin: 0 auto;
		max-width: 550px;
		text-align: center;

		p,
		a {
			@include typography.text-size-4;
		}

		@media (min-width: layout.$breakpoint-desktop) {
			max-width: none;

			p,
			a {
				@include typography.text-size-5;
			}
		}

		p:not(.linkContainer) {
			@media (min-width: 992px) {
				text-align: left;
			}
		}
	}
	@media (min-width: 992px) {
		.listContainer {
			text-align: left;
			margin-left: 5em;
		}
	}

	#mixtinSectionContainer .listContainer {
		@media (min-width: 992px) {
			margin-left: 0;
			margin-right: 5em;
		}
	}
}

.numberedItems {
	margin: 10px 0;
}

.border {
	height: 1px;
	width: 100%;
	margin: 0px auto;
	background-color: #e9e9e9;
}

.sectionHeaders {
	margin: 1.3em 0 0;

	h3 {
		@include typography.text-size-5;
	}

	.updated-time {
		@include typography.text-size-3;

		color: #999;
		margin: var(--spacing-1) 0 var(--spacing-4);
		display: block;
	}

	@media (min-width: 992px) {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-flow: wrap;
		margin: 0.5em 0 var(--spacing-2);
		column-gap: var(--spacing-8);

		.updated-time {
			@include typography.text-size-4;

			margin: 0;
		}
	}
}

.noPadding {
	padding: 0;
}

figure {
	margin: 0;
}

.sectionContainer:not(:last-child) {
	border-bottom: 1px solid #e9e9e9;
}

.sectionContainer:nth-child(2n) {
	@media (min-width: 992px) {
		.imageHolder {
			order: 2;
		}

		.listContainer {
			margin-left: 0;
			margin-right: 50px;
			order: 1;
		}
	}
}

$cssColor1: hsl(200, 70%, 54%);
$cssColor2: hsl(200, 70%, 74%);

$text-animation-duration: 2.5s;

#css3 {
	animation-duration: $text-animation-duration;
	animation-name: cssColorChange;
	animation-iteration-count: infinite;
	animation-direction: alternate;
	animation-delay: 0.5s;
	color: $cssColor1;
	font-weight: bold;
}

$htmlColor1: hsl(17, 80%, 54%);
$htmlColor2: hsl(17, 80%, 74%);

#html5 {
	animation-duration: $text-animation-duration;
	animation-name: htmlColorChange;
	animation-iteration-count: infinite;
	animation-direction: alternate;
	animation-delay: 1s;
	color: $htmlColor1;
	font-weight: bold;
}

$reactColor1: hsl(193, 95%, 68%);
$reactColor2: hsl(193, 95%, 86%);
$darkBorder: #444;

#react {
	animation-duration: $text-animation-duration;
	animation-name: reactColorChange;
	animation-iteration-count: infinite;
	animation-direction: alternate;
	font-weight: bold;
}

.btn {
	display: inline-block;
	border: none;
	padding: 10px 22px;
	border-radius: 3px;
	background-color: #1485cc;
	color: white;
	cursor: pointer;
	margin: 1.5rem 0;

	span {
		margin-left: 4px;
	}

	&:hover {
		color: white;
	}
}

@keyframes reactColorChange {
	from {
		color: $reactColor1;
	}

	to {
		color: $reactColor2;
	}
}

@keyframes cssColorChange {
	from {
		color: $cssColor1;
	}

	to {
		color: $cssColor2;
	}
}

@keyframes htmlColorChange {
	from {
		color: $htmlColor1;
	}

	to {
		color: $htmlColor2;
	}
}
